
<template>
  <div class="params">
    <div id="leixin" v-show="purchaseprice">
      <div class="search_container">
        <el-form :inline="true" :model="params" class="demo-form-inline" ref="filterFormRef">
          <el-form-item label="库存组织" size="mini">
            <el-select clearable v-model="params.company_id" placeholder="请选择" @change="companyChange" filterable @focus="companyListInvok">
              <el-option v-for="item in companyList" :key="item.company_id" :label="item.company_name" :value="item.company_id">
                <span :class="item.type==1 ? 'optionStyle': '' ">{{ item.company_name }}</span>
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="部门" size="mini">
            <el-select clearable v-model="params.department_id" placeholder="请选择" @change="departmentChange" filterable @focus="DepartmentListInvok">
              <el-option :label="item.department_name" :value="item.department_id" :key="item.department_id" v-for="item in departmentList"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="业务日期" size="mini">
            <el-date-picker v-model="timeValue" type="daterange" range-separator="-" value-format="yyyy-MM-dd" start-placeholder="开始日期" end-placeholder="结束日期">
            </el-date-picker>
          </el-form-item>
          <el-form-item label="审核日期" size="mini">
            <el-date-picker v-model="timeValue1" type="daterange" range-separator="-" value-format="yyyy-MM-dd" start-placeholder="开始日期" end-placeholder="结束日期"></el-date-picker>
          </el-form-item>
          <el-form-item label="入库单据号" size="mini">
            <el-input v-model="params.instorage_sn" placeholder="请输入"></el-input>
          </el-form-item>
          <el-form-item label="供应商(厂家)" size="mini">
            <el-select clearable v-model="params.supplier_id" maxlength="100" @change="supplierChangesave" :filter-method="supplierListInvok" @focus="supplierListInvok" filterable placeholder="请选择">
              <el-option v-for="item in supplierList" :label="item.supplier_name" :value="item.supplier_id" :key="item.supplier_id">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="仓库名称" size="mini">
            <el-select v-model="params.warehouse_id" placeholder="请选择" filterable @focus="warehouseInvok" @change="warehouseChange">
              <el-option v-for="item in warehouseList" :key="item.warehouse_id" :label="item.warehouse_name" :value="item.warehouse_id">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="审核状态" size="mini">
            <el-select v-model="params.auditor_status">
              <el-option label="全部" :value="0">全部</el-option>
              <el-option label="未审核" :value="1">未审核</el-option>
              <el-option label="已审核" :value="2">已审核</el-option>
              <el-option label="作废" :value="3">作废</el-option>
            </el-select>
          </el-form-item>

          <el-form-item size="mini" class="search-style search">
            <el-button @click="onSearch" size="mini" type="primary">查询</el-button>
            <el-button plain @click="onReset" size="mini">重置</el-button>
          </el-form-item>
        </el-form>
      </div>
      <div class="add_delet">
        <el-button icon="el-icon-circle-plus-outline" type="primary" @click="addF" class="addBtn" size="mini" v-if="authority.b_add==1">新增</el-button>
        <el-button plain icon="el-icon-delete" @click="deletes" size="mini" v-if="authority.b_del==1">删除</el-button>
        <el-button icon="iconfont icon-pass" type="primary" @click="shenHe" size="mini" v-if="authority.b_pass==1">审核</el-button>
        <el-button plain icon="iconfont icon-zuofei" @click="abolishFun" class="addBtn" size="mini" v-if="authority.b_invalid==1">作废</el-button>
      </div>
      <div class="table_container">
        <el-table :highlight-current-row="true" tooltip-effect="light" border v-loading="loading1" :data="TableData.tableData" :max-height="tableHeight" style="width: 100%" @selection-change="handleSelectionChange">
          <el-table-column :show-overflow-tooltip="true" type="selection" fixed="left" align="center" width="44">
          </el-table-column>
          <el-table-column label="操作" fixed="right" width="100">
            <template slot-scope="scope">
              <el-button @click="seeF(scope, scope.row)" type="text" size="small" v-if="authority.b_info==1">
                查看
              </el-button>
              <el-button @click="addF(scope, scope.row)" type="text" size="small" v-if="scope.row.auditor_status==1&& authority.b_edit==1 && params.type != 5">
                修改
              </el-button>
              <el-button @click="addF(scope, scope.row)" disabled type="text" size="small" v-else>
                修改
              </el-button>
            </template>
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="company_name" width="150" label="库存组织">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="instorage_sn" width="180" label="入库单据号">
          </el-table-column>

          <el-table-column :show-overflow-tooltip="true" property="department_name" width="100" label="部门">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="buyer_name" width="100" label="采购员">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="supplier_name" width="150" label="供应商(厂家)">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="num" width="120" label="总数量" align="right">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="total_abrasion_price" width="120" label="总磨损费" align="right">
            <template slot-scope="scope">
              <pre>{{scope.row.total_abrasion_price  | priceFormatNone}}</pre>
            </template>
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="total_intube_price" width="120" label="总内垫费" align="right">
            <template slot-scope="scope">
              <pre>{{scope.row.total_intube_price  | priceFormatNone}}</pre>
            </template>
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="total_change_price" width="120" label="总换胎收费" align="right">
            <template slot-scope="scope">
              <pre>{{scope.row.total_change_price  | priceFormatNone}}</pre>
            </template>
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="total_price" width="140" label="结算金额" align="right">
            <template slot-scope="scope">
              <pre>{{scope.row.total_price  | priceFormatNone}}</pre>
            </template>
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="type" width="70" label="入库类型">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="warehouse_name" width="132" label="仓库">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="business_time" width="120" label="业务日期">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="add_user_name" width="120" label="创建人">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="add_time" width="170" label="创建时间">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="auditor_name" width="120" label="审核人">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="auditor_time" width="170" label="审核时间">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="auditor_status_name" width="120" label="审核状态">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="remark" width="150" label="备注">
          </el-table-column>
        </el-table>
      </div>
      <div class="page_container">
        <div class="pageNumber">每页显示</div>
        <el-pagination class="erp-pagination" @size-change="handleSizeChange" :current-page.sync="goPage" :page-size="pageSize1" @current-change="handleCurrentChange" :page-sizes="per_page" layout="total,sizes, prev, pager, next,jumper" prevText="上一页" nextText="下一页" :total="TableData.total">
        </el-pagination>
      </div>
    </div>
  </div>
</template>

<script>
import { mapActions, mapGetters } from "vuex";
import {
  getData,
  CompanySwitchClear,
  opthionList,
  tableResize //明细表去除页面滚动条
} from "common/utils/public_function/commonUtils";
export default {
  name: "inventoryThreeinstorageThreeinstorageIndex",
  data() {
    return {
      purchaseprice: true, //int 初始化显示数据列表展示
      examineprice: false, //查看采购计划详情
      lock: 0,
      auditStatus: {
        options: []
      },
      timeValue1: [],
      dialogFormVisible: false,
      tableHeight: 600
    };
  },
  props: {
    type: {
      type: [Number, String],
      default: 1
    }
  },
  computed: {
    ...mapGetters(["threeinstorageIndexGet"])
  },
  methods: {
    ...mapActions([
      "threeinstorageIndexACT",
      "threeinstorageAddACT",
      "threeinstorageSeeACT"
    ]),
    supplierChangesave(val) {
      if (val) {
        this.params.supplier_name = this.supplierList.filter(
          obj => obj.supplier_id === val
        )[0].supplier_name;
      }
    },
    warehouseChange(val) {
      if (val) {
        this.params.warehouse_name = this.warehouseList.filter(
          obj => obj.warehouse_id === val
        )[0].warehouse_name;
      }
    },
    companyChange(val) {
      if (val != "") {
        this.params.company_name = this.companyList.filter(
          obj => obj.company_id === val
        )[0].company_name;
        CompanySwitchClear(this, this.params);
      }
    },
    shenHe() {
      //审核
      if (this.multipleSelection.length < 1) {
        this.$message({
          message: "请选择需要审核的信息！",
          type: "warning"
        });
        return false;
      }
      let addr = [];
      for (var i = 0; i < this.multipleSelection.length; i++) {
        addr.push(this.multipleSelection[i].instorage_id);
      }
      let creditArr = addr.toString();
      let funcan = {
        eachData: {
          instorage_id: creditArr.toString()
        },
        UrlApi: "auditorPassThreeInStorage",
        list: {
          UrlApi: "getThreeInStorageList",
          data: this.TableData,
          esData: this.params
        }
      };
      // 校验对接车郎中
      this.$baseData
        .checkCarDoctor({
          bill_type: 5,
          bill_id: addr.join(","),
          operation_type: 4
        })
        .then(() => {
          this.$common.audit(funcan);
        });
    },
    // 作废
    abolishFun() {
      if (this.multipleSelection.length < 1) {
        this.$message({
          message: "请选择需要作废的信息！",
          type: "warning"
        });
        return false;
      }
      let addr = [];
      for (var i = 0; i < this.multipleSelection.length; i++) {
        addr.push(this.multipleSelection[i].instorage_id);
      }
      let creditArr = addr.toString();
      let funcan = {
        eachData: {
          instorage_id: creditArr.toString()
        },
        UrlApi: "auditorInvalidThreeInStorage",
        list: {
          UrlApi: "getThreeInStorageList",
          data: this.TableData,
          esData: this.params
        }
      };
      // 校验对接车郎中
      this.$baseData
        .checkCarDoctor({
          bill_type: 5,
          bill_id: addr.join(","),
          operation_type: 6
        })
        .then(() => {
          this.$common.audit(funcan);
        });
    },
    departmentChange(val) {
      if (val) {
        this.params.department_name = this.departmentList.filter(
          obj => obj.department_id === val
        )[0].department_name;
      }
    },
    onReset() {
      /** 重置时 如需保存默认参数，则： */
      this.timeValue = [];
      this.timeValue1 = [];
      this.$set(this, "params", { type: this.type, auditor_status: 0 });
      this.threeinstorageIndexACT({});
      this.$set(this, "searchParams", {
        params: this.params,
        TableData: this.TableData, //列表数据
        API: "getThreeInStorageList", //接口名
        vm: this //传递的this
      });
      getData(this.searchParams);
    },
    seeF(scope, row) {
      this.threeinstorageSeeACT({
        instorage_id: row.instorage_id,
        goPage: this.goPage,
        params: this.params,
        pageSize: this.pageSize
      });
      this.$router.push({
        path: "/inventory/threeinstorage/threeinstorageSee"
      });
    },
    addF(scope, row) {
      if (row && row.is_execute == 1) {
        this.$message.error("wms系统正在操作此单据，暂不支持单据修改");
        return false;
      }
      this.$router.push({
        path: "/inventory/threeinstorage/threeinstorageAdd"
      });
      this.threeinstorageAddACT({
        instorage_id: row ? row.instorage_id : null,
        goPage: this.goPage,
        params: this.params,
        pageSize: this.pageSize
      });
    },
    onSearch() {
      if (this.timeValue) {
        this.params.start_time = this.timeValue[0];
        this.params.end_time = this.timeValue[1];
      } else {
        this.params.start_time = this.params.end_time = "";
      }
      if (this.timeValue1) {
        this.params.auditor_start_time = this.timeValue1[0];
        this.params.auditor_end_time = this.timeValue1[1];
      } else {
        this.params.auditor_start_time = this.params.auditor_end_time = "";
      }
      Object.assign(this.searchParams.params, this.params, {
        page: 1
      });
      this.threeinstorageIndexACT({
        goPage: this.goPage,
        params: this.params,
        pageSize: this.pageSize
      });
      getData(this.searchParams);
    },
    //删除
    deletes() {
      if (this.multipleSelection.length < 1) {
        this.$message({
          message: "请选择需要删除的信息！",
          type: "warning"
        });
        return false;
      }
      let addr = [];
      for (var i = 0; i < this.multipleSelection.length; i++) {
        addr.push(this.multipleSelection[i].instorage_id);
      }
      var deledata = {
        deleID: "instorage_id",
        deleSelect: this.multipleSelection,
        deleApi: "delThreeInStorage",
        list: {
          UrlApi: "getThreeInStorageList",
          data: this.TableData,
          esData: this.params
        }
      };
      // 校验对接车郎中
      this.$baseData
        .checkCarDoctor({
          bill_type: 5,
          bill_id: addr.join(","),
          operation_type: 3
        })
        .then(() => {
          this.$common.deletes(deledata, "删除", this);
        });
    }
  },
  mounted() {
    this.$set(this, "params", {
      type: this.type,
      auditor_status: 0
    });
    if (this.threeinstorageIndexGet.goPage) {
      this.params.page = this.threeinstorageIndexGet.goPage;
      this.params.per_page = this.threeinstorageIndexGet.pageSize;
    }
    if (this.threeinstorageIndexGet && this.threeinstorageIndexGet.params) {
      if (this.threeinstorageIndexGet.params.start_time) {
        const { start_time, end_time } = this.threeinstorageIndexGet.params;
        this.timeValue = this.timeValue.concat([start_time, end_time]);
      }
      if (this.threeinstorageIndexGet.params.auditor_start_time) {
        const {
          auditor_start_time,
          auditor_end_time
        } = this.threeinstorageIndexGet.params;
        this.timeValue1 = this.timeValue1.concat([
          auditor_start_time,
          auditor_end_time
        ]);
      }
      this.$set(this, "params", this.threeinstorageIndexGet.params);
      opthionList(this, this.threeinstorageIndexGet.params);
    }
    this.$set(this, "searchParams", {
      params: this.params, //1默认带的参数
      TableData: this.TableData, //列表数据
      API: "getThreeInStorageList", //接口名
      vm: this //传递的this
    });
    getData(this.searchParams, 1, tableResize.bind(this));

    //取消单据右侧滚动条
    window.onresize = tableResize.bind(this);
  }
};
</script>
